body {
  background-color: #100e1d;
}

.card-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.card1 {
  padding: 16px;
  margin: 50px;
  background-color: #fff;
  align-items: center;
  background-color: #100e1d;
}

.search-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 60vh;
}

input {
  width: 30vh;
  height: 50px;
  font-size: 14px;
  border-radius: 1px;
  margin-top: 40px;
  padding-left: 10px;
  border: 0.1px solid #ccc;
  border-radius: 5px;
  background-color: transparent;
  color: white;
}

.search-btn {
  margin-top: 40px;
}

.search-btn button {
  padding: 10px;
  margin-left: 30px;
  cursor: pointer;
}

button {
  appearance: none;
  background-color: transparent;
  border: 0.125em solid #100e1d;
  border-radius: 0.3rem;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  margin: 0;
  min-height: 3.2rem;
  min-width: 0;
  outline: none;
  padding: 1em 2.3em;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  will-change: transform;
}

button:disabled {
  pointer-events: none;
}

button:hover {
  color: #fff;
  background-color: #100e1d;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

button:active {
  box-shadow: none;
  transform: translateY(0);
}

.left-content {
  display: flex;
  height: 20vh;
}

.search-details {
  display: flex;
  flex-direction: column;
  background-color: #252847;
  padding: 30px 50px;
  height: 100vh;
}

.first-3-div {
  display: flex;
  flex-direction: row;
  padding: 10px;
  background-color: #100e1d;
}

.last-3-div {
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: center;
  background-color: #100e1d;
  align-items: flex-start;
}

.card2 {
  padding: 30px;
  margin: 20px;
  background-color: aqua;
  margin: 20px;
  background-color: #252847;
  color: white;
  border-radius: 12px;
  display: flex;
  align-items: center;
}

.card2-part2 {
  display: flex;
  flex-direction: column;
}

.card2-part2 h1 {
  font-size: 3.5rem;
}

.card3 {
  padding: 30px;
  margin: 20px;
  width: 200px;
  background-color: #252847;
  color: white;
  border-radius: 12px;
}

.icons {
  margin-left: 370px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icons img {
  width: 90px;
  height: 90px;
}

.icons h2 {
  font-size: 1.5rem;
  align-items: end;
}

.card4,
.card5 {
  display: flex;
  flex-direction: column;
}

.card4-parent,
.card5-parent {
  display: flex;
  align-items: center;
  background-color: #252847;
  padding: 30px;
  margin: 20px;
  color: white;
  border-radius: 12px;
}

.card4-parent img,
.card5-parent img {
  height: 40px;
  width: 40px;
  margin-left: 25px;
}

@media (max-width: 1068px) {
  body {
    padding: 20px;
  }

  .card-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .card1 {
    margin: 20px;
  }

  .search-box {
    flex-direction: column;
    align-items: center;
    width: 90%;
  }

  input {
    width: 100%;
    margin-top: 20px;
  }

  .search-btn {
    margin-top: 20px;
  }

  button {
    margin-left: 0;
  }

  .left-content {
    flex-direction: column;
    align-items: center;
    height: auto;
  }

  .search-details {
    display: flex;
    background-color: #252847;
    padding: 30px 50px;
    height: auto;
    flex-direction: column;
    width: 50vh;
    border-radius: 12px;
  }

  .first-3-div,
  .last-3-div {
    flex-direction: column;
    padding: 0;
  }

  .card2,
  .card3 {
    margin: 10px;
  }

  .icons {
    margin-left: 0;
  }

  .card4-parent,
  .card5-parent {
    margin: 10px;
  }
}


